
import { ethers } from 'ethers'
import { TApiSource } from '../types'

const computeScanAddress = async (
  qrSecret: string,
  qrEncCode: string
) => {
  try {
    const linkKey = ethers.id(qrSecret)
    const qrKeysPair = new ethers.Wallet(linkKey)
    const MULTISCAN_QR_ID = qrKeysPair.address.toLowerCase()

    let redirectURL = ''

    const SCAN_ID = String(Math.random()).slice(2)
    
    const SCAN_ID_SIG = await qrKeysPair.signMessage(`Dispenser Scan Id: ${SCAN_ID}`)
 
    return `/scan/${MULTISCAN_QR_ID}/${SCAN_ID}/${SCAN_ID_SIG}/${qrEncCode}`

  } catch (err: any) {
    console.error(err)
  }
}

export default computeScanAddress

