'use server'
import { computeScanAddress } from '@/utils'
import {
  FC
} from 'react'
import Content from './content'

async function Verifier ({
  params
}: {
  params: {
    qr_secret: string,
    qr_enc_code: string
  }
}) {

  const redirectUrl  = await computeScanAddress(
    params.qr_secret,
    params.qr_enc_code
  )

  if (redirectUrl) {
    return <Content
      scanLink={redirectUrl}
    />
  }

  return <h1>Error</h1>


}

export default Verifier

